.article {
	padding: 22px 0 34px 42px;
	border-radius: 10px;

	@include r(608) {
		padding: 10px 10px 0; }

	&--about {
		background: #fff url('../img/bg/article1-bg.png') no-repeat 100% 0%;

		@include r(980) {
			background-size: cover;
			background-color: black;
			background-attachment: fixed;
			color: #fff;

			.article__title {
				color: #fff; }

			.article__text-inner {
				padding: 150px 0; } }

		@include r(400) {
			background: black;

			.article__text-inner {
				padding: 0; } } }

	&--recommend {
		background: #fff url('../img/bg/article2-bg.png') no-repeat 100% 0%;
		display: flex;
		justify-content: space-between;
		padding-bottom: 0;
		font-size: 16px;

		@include r(980) {
			align-items: flex-end;
			background-size: cover; }

		@include r(750) {
			flex-wrap: wrap;
			background-attachment: fixed; } }

	&__text-inner {
		width: 60%;

		@include r(750) {
			width: 100%; } }

	&__title {
		padding-bottom: 21px;
		color: #000;
		font-weight: 700;
		line-height: 52px; }

	&__text {
		@include font-ptsans(400);
		font-size: 17px;
		line-height: 26px;

		&--width {
			@include font-ptsans(700);
			text-transform: uppercase; }

		&--padding {
			padding-bottom: 10px;
			line-height: 24px; } }

	&__photo {
		width: 39%;

		@include r(608) {
			width: 70%; }

		@include r(400) {
			width: 100%; } }


	&__byline {
		display: flex;
		align-items: center;

		@include r(400) {
			flex-wrap: wrap;
			justify-content: center;
			padding: 10px 0; }

		&-text {
			@include font-ptsans(700);
			font-style: italic;
			padding-right: 48px;

			@include r(608) {
				padding-right: 20px; } } } }


.subtitle--spacing {
	word-spacing: -10px; }



