.features {
  padding: 11px 0 5px;
}

.features__item {
  position: relative;
  font-family: 'PT Sans', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  padding-bottom: 10px;
}

.features__item:before {
  content: url("../img/icons/features-list.png");
  display: inline-block;
  padding: 5px 3px;
  border: 1px solid #b67a52;
  border-radius: 24px;
  background: #d23d16;
  line-height: 0;
}

.features__item:after {
  content: '';
  display: block;
  position: absolute;
  top: 100%;
  left: 0px;
  margin-top: -2px;
  width: 40%;
  height: 1px;
  background: radial-gradient(circle rgba(255, 255, 255, 0.3), transparent);
}

.features__item:last-child {
  padding-top: 4px;
  line-height: 23px;
}

.features__item:last-child:before {
  vertical-align: top;
}

.features__item:last-child:after {
  display: none;
}

.features__text {
  display: inline-block;
  padding-left: 5px;
}

