.form {
  font-family: 'PT Sans Narrow', sans-serif;
  font-weight: 400;
  background: #ffffff;
  border-radius: 10px;
}

.form__head {
  display: flex;
  clip-path: ellipse(92% 65% at 50% 23%);
  background: radial-gradient(circle farthest-side at 20px 30px, #972e09 20%, #401208);
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.form__head-title {
  font-size: 18px;
}

.form__head-price {
  font-size: 48px;
}

.form__head-text {
  font-size: 24px;
}

.form__head-left {
  padding: 14px 0 34px 22px;
  width: 53%;
  color: #845f55;
  background: rgba(144, 53, 19, 0.3);
  clip-path: ellipse(90% 100% at 10% 46%);
}

.form__head-left .form__head-price {
  text-decoration: line-through;
}

.form__head-right {
  color: #fff;
  padding: 13px 0 34px 15px;
}

.form__head-right .form__head-price,
.form__head-right .form__head-text {
  font-family: 'PT Sans Narrow', sans-serif;
  font-weight: 700;
}

.form__main {
  padding: 0 17px 21px;
}

.form__input {
  width: 100%;
  padding: 20px 0 20px 15px;
  margin-bottom: 15px;
  background: #f6f6f6;
  border: 1px solid #e1e1e1;
  border-radius: 5px;
  font-size: 14px;
}

.form__input::placeholder {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  color: #929292;
}

.form__btn {
  width: 100%;
  position: relative;
  margin: 8px 0 21px;
  padding: 17px 0 17px 34px;
  font-family: 'PT Sans Narrow', sans-serif;
  font-weight: 700;
  color: #fff;
  font-size: 22px;
  background-image: linear-gradient(to top, #d73f17 0%, #fa4a1b 100%);
  box-shadow: 3px 4px 25px rgba(250, 74, 27, 0.35);
  border-radius: 5px;
}

.form__btn:before {
  content: url("../img/icons/form-arrow.png");
  position: absolute;
  top: 0%;
  left: 0%;
  padding: 19px 20px 14px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  background: #b13412;
}

.form__btn:hover {
  background: #b13412;
}

.form__meta {
  font-size: 15px;
  text-align: center;
}

.form__meta:before {
  content: url("../img/icons/form-icon.png");
  display: inline-block;
  vertical-align: middle;
  padding-right: 10px;
}

