.advantage {
	background: url('../img/bg/advantage-bg.png') no-repeat 50% 0%;
	background-size: contain;
	padding: 43px 0 60px;
	text-align: center;

	@include r(1320) {
		background-size: cover; }

	@include r(800) {
		background: black; }

	&__content {
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		flex-wrap: wrap;
		padding-top: 30px; }

	&__photo {
		width: 31%;

		@include r(800) {
			display: none; } }

	&__list {
		display: flex;
		justify-content: space-around;
		flex-wrap: wrap;
		width: 67%;

		@include r(800) {
			width: 100%; } }


	&__item {
		width: 30.5%;
		margin-bottom: 15px;
		background: #fff;
		border-radius: 10px;

		@include r(950) {
			min-width: 250px; }


		&-text {
			padding: 17px 3px;
			@include font-ptsans(700);
			color: #0b0b0b;
			font-size: 16px; } } }

